import React from 'react'
import Link from 'gatsby-link'
import imgCF from './jake.jpg';
import BackNav from '../components/BackNav'

const GustoOffers = () =>
  <div>
    <div className="main work-page relative">
      <BackNav />
      <h1 className='huge-text'>Offer Letters @ Gusto</h1>
      <p>After initial research was clear we needed to design a next level solution for offers.</p>
      <img className="card img-full" src={imgCF} />
      <p>Designing delight</p>
      <img className="card img-full" src={imgCF} />
      <p>Here's all the things about offer letters.</p>
      <img className="card img-full" src={imgCF} />
      <p>Here's all the things about offer letters.</p>
      <img className="card img-full" src={imgCF} />
      <p>Here's all the things about offer letters.</p>
      <img className="card img-full" src={imgCF} />
      <p>Here's all the things about offer letters.</p>
      <img className="card img-full" src={imgCF} />
    </div>
    <div className="rainbow block top-slice padding-bottom-50">
      <div className="main white-text margin-top-none">
        <div className="footer-links-list">
          <Link to='/' className='white-text'><h2>Back to Home</h2></Link>
        </div>
        <h1 className="">Other work bits</h1>
        <div className="footer-links-list">
          <Link to="/case" >Gusto</Link>
          <Link to="/case" >Crossfader</Link>
          <Link to="/case" >Feathr</Link>
        </div>
      </div>
    </div>
  </div>

export default GustoOffers



